.layout-container-top {
  &.module {
    .what-learning-title {
      margin-top: 40px;
      font-weight: 900;
    }
    .what-learning-title-container {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      margin: 10px 0px;
      .what-learning-item {
        flex: 0 0 40%;
        svg {
          margin-right: 20px;
        }
      }
    }
  }
}
.layout-container-body {
  &.module {
    margin-top: 40px;
    span {
      line-height: 41px;
      &.total-sections {
        margin-left: auto;
        margin-right: 10px;
      }
      &.total-lectures {
        margin-left: 10px;
      }
      &.icon-circle {
        font-size: 60px;
        line-height: 32px;
      }
    }
  }

  .accordion-item {
    .what-learning-title {
      font-weight: 900;
    }
    .accordion-header {
      button {
        background-color: #f5f5f5;
        font-weight: 600;
      }
    }
    .accordion-body {
      background-color: white;
      a {
        display: block;
        padding: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.03);
        &:hover {
          background-color: rgba(14, 76, 140, 0.05);
        }
      }
    }
  }
}
